$primary: #4ecca3;

.sample-wrapper{
    img{
        width: 100%;
        z-index: 1;
        border-radius: .25em;
        box-shadow: 0px 3px 3px -2px rgba(0, 0, 0, 0.2), 0px 3px 4px 0px rgba(0, 0, 0, 0.14), 0px 1px 8px 0px rgba(0, 0, 0, 0.12);
    }
}

.sample-wrapper:hover{
    cursor: pointer;
    .sample-subtitle-wrapper{
        color: $primary;
    }
}


.sample-subtitle{
    font-family: acumin-pro, sans-serif;
    font-weight: 700;
    font-style: normal;
    color: #FFFFFF;
    opacity: 1;
    margin-top: -50%;
    margin-bottom: 40%;
    font-size: 1.6rem;
    display: flex;
    justify-content: center;
    z-index: 10;
}


@media (max-width: 720px){
    .sample-subtitle{
        font-size: 1.2rem;
        margin-top: -55%;
        margin-bottom: 45%;
    }
}

@media (max-width: 600px){
    .sample-subtitle{
        font-size: 1.2rem;
        margin-top: -55%;
        margin-bottom: 45%;
    }
}

@media (max-width: 375px){
    .sample-subtitle{
        font-size: 1rem;
        margin-top: -55%;
        margin-bottom: 45%;
    }
}


