   .sample-wrapper{
    .sample-subtitle-wrapper{
        text-decoration: none;        
        display: inline-block;
        padding: 15px 20px;
        position: relative;
      }
   } 
  .sample-wrapper{
    .sample-subtitle-wrapper:after{    
        background: none repeat scroll 0 0 transparent;
        bottom: 0;
        content: "";
        display: block;
        height: 2px;
        left: 50%;
        position: absolute;
        background: $primary;
        transition: width 0.3s ease 0s, left 0.3s ease 0s;
        width: 0;
        color: $primary;
      }
  } 
  .sample-wrapper:hover{
    .sample-subtitle-wrapper:after{
        color: $primary;
        width: 100%; 
        left: 0; 
      }
  } 
  @media screen and (max-height: 300px) {
      ul {
          margin-top: 40px;
      }
  }